#offerings {
    height: 100vh;
    margin-top: 10vh;
    background-color: #214f99;
}

#offerings h1 {
    font-family: 'Orbitron', sans-serif;
    text-align: center;
}

#offerings table {
    display: block;
    border: 1px solid #ddd;
    width: 75vw;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 1%;
    overflow-y: scroll;
}

#offerings thead th { 
    position: sticky; 
    top: 0;
    background-color: white; 
}

#offerings tr, th, td {
    border-bottom: 1px solid #ddd;
}