@import url('https://fonts.googleapis.com/css?family=Orbitron');

.globalNavbar {
    margin-top: 3%;
    margin-left: 5%;
    margin-right: 5%;

    transition-property: all;
    transition-duration: 1s;
    transition-timing-function: ease;
}

/*editing links*/
.globalNavbar .links {
    margin-left: 5%;
}

.globalNavbar a {
    font-family: 'Orbitron', sans-serif;
    color: #8590a3;
    display: inline-block;
    padding: 0.5em 1em 1em 1em;
    margin-top: 1%;
}

.globalNavbar a:link {
    text-decoration: none;
}

.globalNavbar a:hover {
    background-color:#dee2e8;
}

.globalNavbar a:active {
    color: none;
}

.mobileNavbar {
    height: 7vh;
    background-color: #191a1c;
}

.mobileNavbar h3 {
    font-family: 'Orbitron', sans-serif;
    color: #8590a3;
    display: inline-block;
}

.mobileNavbar .mobileHamburger {
    height: 75%;
    float: right;
}

/*editing name*/
.globalNavbar .name {
    float: right;
    margin-right: 5%;
    margin-top: 1%;
}

/*home page div*/
.homPage {
    height: 90vh;
}

.homPage #title {
    display: block;
    text-align: center;
    padding-top: 30vh;
    width: 100%;

    font-size: 2em;

    font-family: 'Orbitron', sans-serif;

    animation-duration: 2s;
    animation-name: title-fadein;
}

@keyframes title-fadein {
    from {
        margin-left: 100%;
    }

    to {
        margin-left: 0%;
    }
}

/*mobile navbar*/

#logo {
    font-family: 'Orbitron', sans-serif;
    color: #8590a3;
}

#dropdown-list {
    font-family: 'Orbitron', sans-serif;
    color: #8590a3;
}