@import url(https://fonts.googleapis.com/css?family=Orbitron);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#aboutMe {
    height: 100vh;
    margin-top: 10vh;
    background-color: #214f99;
}

#aboutMe h1 {
    font-family: 'Orbitron', sans-serif;
    text-align: center;
}

#aboutMe p {
    width: 50vw;
    margin: auto;
}

#contactMe h3 {
    font-family: 'Orbitron', sans-serif;
    text-align: center;
}

#contactMe p {
    text-align: center;
}
#offerings {
    height: 100vh;
    margin-top: 10vh;
    background-color: #214f99;
}

#offerings h1 {
    font-family: 'Orbitron', sans-serif;
    text-align: center;
}

#offerings table {
    display: block;
    border: 1px solid #ddd;
    width: 75vw;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 1%;
    overflow-y: scroll;
}

#offerings thead th { 
    position: -webkit-sticky; 
    position: sticky; 
    top: 0;
    background-color: white; 
}

#offerings tr, th, td {
    border-bottom: 1px solid #ddd;
}
#bookMe {
    height: 100vh;
    margin-top: 10vh;
}

#bookMe h1 {
    font-family: 'Orbitron', sans-serif;
    text-align: center;
}

#bookMe p {
    text-align: center;
}
.globalNavbar {
    margin-top: 3%;
    margin-left: 5%;
    margin-right: 5%;

    transition-property: all;
    transition-duration: 1s;
    transition-timing-function: ease;
}

/*editing links*/
.globalNavbar .links {
    margin-left: 5%;
}

.globalNavbar a {
    font-family: 'Orbitron', sans-serif;
    color: #8590a3;
    display: inline-block;
    padding: 0.5em 1em 1em 1em;
    margin-top: 1%;
}

.globalNavbar a:link {
    text-decoration: none;
}

.globalNavbar a:hover {
    background-color:#dee2e8;
}

.globalNavbar a:active {
    color: none;
}

.mobileNavbar {
    height: 7vh;
    background-color: #191a1c;
}

.mobileNavbar h3 {
    font-family: 'Orbitron', sans-serif;
    color: #8590a3;
    display: inline-block;
}

.mobileNavbar .mobileHamburger {
    height: 75%;
    float: right;
}

/*editing name*/
.globalNavbar .name {
    float: right;
    margin-right: 5%;
    margin-top: 1%;
}

/*home page div*/
.homPage {
    height: 90vh;
}

.homPage #title {
    display: block;
    text-align: center;
    padding-top: 30vh;
    width: 100%;

    font-size: 2em;

    font-family: 'Orbitron', sans-serif;

    -webkit-animation-duration: 2s;

            animation-duration: 2s;
    -webkit-animation-name: title-fadein;
            animation-name: title-fadein;
}

@-webkit-keyframes title-fadein {
    from {
        margin-left: 100%;
    }

    to {
        margin-left: 0%;
    }
}

@keyframes title-fadein {
    from {
        margin-left: 100%;
    }

    to {
        margin-left: 0%;
    }
}

